import React, { useState } from 'react';
import { useExpenses, useSummary } from '../hooks/useExpenses';
import FinancialDashboard from '../components/FinancialDashboard';
import AddIncome from '../components/AddIncome';
import BudgetManager from '../components/BudgetManager';
import ExpenseList from '../components/ExpenseList';
import { Button } from '../components/ui/Button';
import { BarChart3, List } from 'lucide-react';

const Dashboard: React.FC = () => {
  const { expenses, loading: expensesLoading, addExpense, fetchExpenses } = useExpenses();
  const { summary, loading: summaryLoading, fetchSummary } = useSummary();
  const [showAddIncome, setShowAddIncome] = useState(false);
  const [showBudgetManager, setShowBudgetManager] = useState(false);
  const [showExpenseList, setShowExpenseList] = useState(false);

  const loading = expensesLoading || summaryLoading;

  // Gerar dados reais baseados nas despesas da API
  const generateRealMonthlySummary = () => {
    const currentMonth = new Date().toISOString().slice(0, 7);
    const monthlyExpenses = expenses.filter(expense => 
      expense.date.startsWith(currentMonth)
    );

    const totalExpenses = monthlyExpenses.reduce((sum, exp) => sum + exp.amount, 0);
    const totalIncome = 5000; // Mock income - em produo viria da API
    const totalSavings = totalIncome - totalExpenses;
    const savingsPercentage = totalIncome > 0 ? (totalSavings / totalIncome) * 100 : 0;

    // Agrupar por categoria
    const categorySummary = monthlyExpenses.reduce((acc, expense) => {
      const category = expense.category;
      if (!acc[category]) {
        acc[category] = {
          category,
          count: 0,
          total: 0,
          average: 0,
          budget_amount: getDefaultBudget(category),
          remaining_amount: 0,
          percentage_used: 0
        };
      }
      acc[category].count += 1;
      acc[category].total += expense.amount;
      acc[category].average = acc[category].total / acc[category].count;
      return acc;
    }, {} as { [key: string]: any });

    // Calcular remaining_amount e percentage_used
    Object.values(categorySummary).forEach((cat: any) => {
      cat.remaining_amount = cat.budget_amount - cat.total;
      cat.percentage_used = cat.budget_amount > 0 ? (cat.total / cat.budget_amount) * 100 : 0;
    });

    return {
      month: currentMonth,
      total_income: totalIncome,
      total_expenses: totalExpenses,
      total_savings: totalSavings,
      savings_percentage: savingsPercentage,
      remaining_balance: totalSavings,
      budget_summary: Object.values(categorySummary)
    };
  };

  // Oramentos padro por categoria
  const getDefaultBudget = (category: string): number => {
    const budgets: { [key: string]: number } = {
      'food': 1000,
      'transport': 500,
      'health': 300,
      'housing': 1500,
      'leisure': 400,
      'education': 200,
      'utilities': 300,
      'shopping': 500,
      'entertainment': 250,
      'other': 150
    };
    return budgets[category] || 100;
  };

  const monthlySummary = generateRealMonthlySummary();

  const handleAddIncome = () => {
    setShowAddIncome(true);
  };

  const handleAddExpense = () => {
    setShowExpenseList(true);
  };

  const handleEditBudget = (category: string) => {
    setShowBudgetManager(true);
  };

  const handleSaveBudget = (budgets: any[]) => {
    console.log('Saving budgets:', budgets);
    setShowBudgetManager(false);
  };

  const handleIncomeAdded = (income: any) => {
    console.log('Income added:', income);
    setShowAddIncome(false);
    // Refresh data after adding income
    fetchExpenses();
    fetchSummary();
  };

  const handleExpenseAdded = () => {
    // Refresh data after adding expense
    fetchExpenses();
    fetchSummary();
  };

  return (
    <div className="space-y-6">
      {/* Navigation Tabs */}
      <div className="flex space-x-2 border-b border-gray-200">
        <Button
          variant={!showExpenseList ? "primary" : "outline"}
          onClick={() => setShowExpenseList(false)}
          className="flex items-center space-x-2"
        >
          <BarChart3 className="h-4 w-4" />
          <span>Dashboard</span>
        </Button>
        <Button
          variant={showExpenseList ? "primary" : "outline"}
          onClick={() => setShowExpenseList(true)}
          className="flex items-center space-x-2"
        >
          <List className="h-4 w-4" />
          <span>Expense List</span>
        </Button>
      </div>

      {/* Main Content */}
      {showExpenseList ? (
        <ExpenseList />
      ) : (
        <FinancialDashboard
          monthlySummary={monthlySummary}
          loading={loading}
          onAddIncome={handleAddIncome}
          onAddExpense={handleAddExpense}
          onEditBudget={handleEditBudget}
        />
      )}

      {/* Modals */}
      {showAddIncome && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="w-full max-w-md">
            <AddIncome
              onIncomeAdded={handleIncomeAdded}
              onClose={() => setShowAddIncome(false)}
            />
          </div>
        </div>
      )}

      <BudgetManager
        isOpen={showBudgetManager}
        onClose={() => setShowBudgetManager(false)}
        onSave={handleSaveBudget}
        currentBudgets={monthlySummary.budget_summary.reduce((acc, item) => {
          acc[item.category] = item.budget_amount;
          return acc;
        }, {} as { [key: string]: number })}
      />
    </div>
  );
};

export default Dashboard;