import React, { useState } from 'react';
import { 
  DollarSign, 
  TrendingUp, 
  TrendingDown, 
  Target, 
  PiggyBank,
  Plus,
  Minus,
  AlertTriangle,
  CheckCircle
} from 'lucide-react';
import { Card, CardHeader, CardBody } from './ui/Card';
import { Button } from './ui/Button';
import { Badge } from './ui/Badge';
import { Loading } from './ui/Loading';
import { 
  CATEGORY_COLORS, 
  CATEGORY_ICONS, 
  CATEGORY_LABELS, 
  type MonthlySummary,
  type CategorySummary 
} from '../types';
import { formatCurrency } from '../utils/format';

interface FinancialDashboardProps {
  monthlySummary: MonthlySummary | null;
  loading: boolean;
  onAddIncome: () => void;
  onAddExpense: () => void;
  onEditBudget: (category: string) => void;
}

const FinancialDashboard: React.FC<FinancialDashboardProps> = ({
  monthlySummary,
  loading,
  onAddIncome,
  onAddExpense,
  onEditBudget
}) => {
  const [selectedMonth, setSelectedMonth] = useState(new Date().toISOString().slice(0, 7));

  if (loading) {
    return (
      <div className="space-y-6">
        <Loading text="Carregando dashboard financeiro..." className="py-8" />
      </div>
    );
  }

  if (!monthlySummary) {
    return (
      <div className="text-center py-8">
        <p className="text-gray-600 mb-4">Nenhum dado financeiro encontrado</p>
        <Button onClick={onAddIncome} className="mr-2">
          <Plus className="h-4 w-4 mr-2" />
          Adicionar Entrada
        </Button>
        <Button onClick={onAddExpense} variant="outline">
          <Minus className="h-4 w-4 mr-2" />
          Adicionar Despesa
        </Button>
      </div>
    );
  }

  const { total_income, total_expenses, total_savings, savings_percentage, remaining_balance, budget_summary } = monthlySummary;
  const savingsTarget = total_income * 0.20; // 20% do salrio
  const isSavingsTargetMet = total_savings >= savingsTarget;

  return (
    <div className="space-y-6">
      {/* Header com seleo de ms */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Financial Dashboard</h1>
          <p className="text-gray-600">Overview for {new Date(selectedMonth + '-01').toLocaleDateString('en-US', { month: 'long', year: 'numeric' })}</p>
        </div>
        <div className="flex items-center space-x-2">
          <input
            type="month"
            value={selectedMonth}
            onChange={(e) => setSelectedMonth(e.target.value)}
            className="px-3 py-2 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-primary-500"
          />
        </div>
      </div>

      {/* Cards de Resumo */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {/* Income */}
        <Card>
          <CardBody className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Income</p>
                <p className="text-2xl font-bold text-green-600">{formatCurrency(total_income)}</p>
              </div>
              <div className="p-3 bg-green-100 rounded-full">
                <TrendingUp className="h-6 w-6 text-green-600" />
              </div>
            </div>
            <Button 
              onClick={onAddIncome} 
              size="sm" 
              className="mt-3 w-full bg-green-600 hover:bg-green-700"
            >
              <Plus className="h-4 w-4 mr-2" />
              Add
            </Button>
          </CardBody>
        </Card>

        {/* Expenses */}
        <Card>
          <CardBody className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Expenses</p>
                <p className="text-2xl font-bold text-red-600">{formatCurrency(total_expenses)}</p>
              </div>
              <div className="p-3 bg-red-100 rounded-full">
                <TrendingDown className="h-6 w-6 text-red-600" />
              </div>
            </div>
            <Button 
              onClick={onAddExpense} 
              size="sm" 
              className="mt-3 w-full bg-red-600 hover:bg-red-700"
            >
              <Minus className="h-4 w-4 mr-2" />
              Add
            </Button>
          </CardBody>
        </Card>

        {/* Savings */}
        <Card>
          <CardBody className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Savings</p>
                <p className="text-2xl font-bold text-blue-600">{formatCurrency(total_savings)}</p>
                <p className="text-xs text-gray-500">{savings_percentage.toFixed(1)}% of income</p>
              </div>
              <div className="p-3 bg-blue-100 rounded-full">
                <PiggyBank className="h-6 w-6 text-blue-600" />
              </div>
            </div>
            <div className="mt-3">
              {isSavingsTargetMet ? (
                <Badge variant="success" className="w-full justify-center">
                  <CheckCircle className="h-3 w-3 mr-1" />
                  Target met!
                </Badge>
              ) : (
                <Badge variant="warning" className="w-full justify-center">
                  <AlertTriangle className="h-3 w-3 mr-1" />
                  Target: {formatCurrency(savingsTarget)}
                </Badge>
              )}
            </div>
          </CardBody>
        </Card>

        {/* Remaining Balance */}
        <Card>
          <CardBody className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Remaining Balance</p>
                <p className={`text-2xl font-bold ${remaining_balance >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                  {formatCurrency(remaining_balance)}
                </p>
              </div>
              <div className={`p-3 rounded-full ${remaining_balance >= 0 ? 'bg-green-100' : 'bg-red-100'}`}>
                <DollarSign className={`h-6 w-6 ${remaining_balance >= 0 ? 'text-green-600' : 'text-red-600'}`} />
              </div>
            </div>
          </CardBody>
        </Card>
      </div>

      {/* Budget by Category */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold text-gray-900">Budget by Category</h2>
            <Button onClick={() => onEditBudget('all')} variant="outline" size="sm">
              <Target className="h-4 w-4 mr-2" />
              Manage Budget
            </Button>
          </div>
        </CardHeader>
        <CardBody>
          <div className="space-y-4">
            {budget_summary.map((category) => (
              <CategoryBudgetCard 
                key={category.category} 
                category={category} 
                onEdit={() => onEditBudget(category.category)}
              />
            ))}
          </div>
        </CardBody>
      </Card>

      {/* Savings Progress */}
      <Card>
        <CardHeader>
          <h2 className="text-xl font-semibold text-gray-900">Savings Progress</h2>
        </CardHeader>
        <CardBody>
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <span className="text-sm font-medium text-gray-600">Savings Target (20%)</span>
              <span className="text-sm text-gray-600">{formatCurrency(savingsTarget)}</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-3">
              <div 
                className={`h-3 rounded-full transition-all duration-300 ${
                  isSavingsTargetMet ? 'bg-green-500' : 'bg-blue-500'
                }`}
                style={{ width: `${Math.min((total_savings / savingsTarget) * 100, 100)}%` }}
              />
            </div>
            <div className="flex justify-between text-sm text-gray-600">
              <span>Saved: {formatCurrency(total_savings)}</span>
              <span>{((total_savings / savingsTarget) * 100).toFixed(1)}%</span>
            </div>
          </div>
        </CardBody>
      </Card>
    </div>
  );
};

interface CategoryBudgetCardProps {
  category: CategorySummary;
  onEdit: () => void;
}

const CategoryBudgetCard: React.FC<CategoryBudgetCardProps> = ({ category, onEdit }) => {
  const { category: categoryKey, total, budget_amount, remaining_amount } = category;
  const isOverBudget = total > budget_amount;
  const percentage = budget_amount > 0 ? (total / budget_amount) * 100 : 0;

  return (
    <div className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow">
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center space-x-3">
          <div 
            className="w-10 h-10 rounded-full flex items-center justify-center text-white text-lg"
            style={{ backgroundColor: CATEGORY_COLORS[categoryKey] }}
          >
            {CATEGORY_ICONS[categoryKey]}
          </div>
          <div>
            <h3 className="font-medium text-gray-900">{CATEGORY_LABELS[categoryKey]}</h3>
            <p className="text-sm text-gray-500">
              {formatCurrency(total)} of {formatCurrency(budget_amount)}
            </p>
          </div>
        </div>
        <div className="text-right">
          <p className={`text-sm font-medium ${isOverBudget ? 'text-red-600' : 'text-green-600'}`}>
            {isOverBudget ? `+${formatCurrency(Math.abs(remaining_amount))}` : formatCurrency(remaining_amount)}
          </p>
          <p className="text-xs text-gray-500">
            {isOverBudget ? 'Over budget' : 'Remaining'}
          </p>
        </div>
      </div>
      
      <div className="space-y-2">
        <div className="flex justify-between text-sm">
          <span className="text-gray-600">Progress</span>
          <span className="text-gray-600">{percentage.toFixed(1)}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-2">
          <div 
            className={`h-2 rounded-full transition-all duration-300 ${
              isOverBudget ? 'bg-red-500' : percentage > 80 ? 'bg-yellow-500' : 'bg-green-500'
            }`}
            style={{ width: `${Math.min(percentage, 100)}%` }}
          />
        </div>
        <div className="flex justify-between text-xs text-gray-500">
          <span>Spent: {formatCurrency(total)}</span>
          <span>Budget: {formatCurrency(budget_amount)}</span>
        </div>
      </div>
      
      <Button 
        onClick={onEdit} 
        variant="outline" 
        size="sm" 
        className="mt-3 w-full"
      >
        <Target className="h-3 w-3 mr-2" />
        Adjust Budget
      </Button>
    </div>
  );
};

export default FinancialDashboard;
