import React, { useState, useRef, useEffect } from 'react';
import { Bot, Send, X, Sparkles, DollarSign, TrendingUp, Camera } from 'lucide-react';
import { Button } from './ui/Button';
import { expenseService } from '../services/api';
import { useToast } from './ui/Toast';

interface Message {
  id: string;
  text: string;
  isUser: boolean;
  timestamp: Date;
  type?: 'text' | 'suggestion' | 'action';
  data?: any;
}

interface XuzinhaChatProps {
  onExpenseAdded?: () => void;
}

const XuzinhaChat: React.FC<XuzinhaChatProps> = ({ onExpenseAdded }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: 'Hello! I\'m Xuzinha, your personal financial assistant! \n\nI can help you with:\n Categorize expenses\n Analyze your spending\n Give savings tips\n Answer financial questions\n\nHow can I help you today?',
      isUser: false,
      timestamp: new Date(),
      type: 'text'
    }
  ]);
  const [inputText, setInputText] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [isScanning, setIsScanning] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { addToast } = useToast();

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const quickActions = [
    { text: 'Categorize expense', icon: DollarSign, action: 'categorize' },
    { text: 'Scan receipt', icon: Camera, action: 'scan' },
    { text: 'View summary', icon: TrendingUp, action: 'summary' },
    { text: 'Savings tips', icon: Sparkles, action: 'tips' }
  ];

  const xuzinhaResponses = {
    categorize: [
      "I'll analyze your expense! \n\nTo categorize, tell me what you spent and how much. For example:\n 'Lunch at restaurant 45 reais'\n 'Gasoline 80 reais'\n 'Pharmacy 25 reais'\n\nWhat was your expense?",
      "Perfect! Tell me about your expense and I'll categorize it automatically! "
    ],
    scan: [
      "I'll scan your receipt! \n\nClick the camera button to take a photo of the receipt or select an image. I'll extract:\n Total amount\n Date\n Merchant\n Automatic category\n\nReady to scan?",
      "OCR activated! \n\nTake a photo of the receipt and I'll do all the work for you!"
    ],
    summary: [
      "I'll show your financial summary! \n\nGo to the 'Summary' tab to see:\n Charts by category\n Total spent\n Trends\n Smart analysis\n\nWant me to explain any specific data?",
      "Your summary is on the Summary page! I can explain any metric for you! "
    ],
    tips: [
      "Here are my savings tips! \n\n 50/30/20: 50% needs, 30% wants, 20% savings\n Use the 24h rule for big purchases\n Automate your savings\n Review expenses monthly\n Compare prices before buying\n\nWant specific tips for any category?",
      "Personalized savings tips for you! \n\nI can give specific advice based on your spending!"
    ]
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputText,
      isUser: true,
      timestamp: new Date(),
      type: 'text'
    };

    setMessages(prev => [...prev, userMessage]);
    const currentInput = inputText;
    setInputText('');
    setIsTyping(true);

    // Processar com IA inteligente
    try {
      const response = await processIntelligentResponse(currentInput);
      const xuzinhaMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: response,
        isUser: false,
        timestamp: new Date(),
        type: 'text'
      };
      
      setMessages(prev => [...prev, xuzinhaMessage]);
    } catch (error) {
      console.error('Error processing message:', error);
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: "Ops! Algo deu errado. Tente novamente! ",
        isUser: false,
        timestamp: new Date(),
        type: 'text'
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsTyping(false);
    }
  };

  // Funo de processamento inteligente com IA
  const processIntelligentResponse = async (input: string): Promise<string> => {
    const lowerInput = input.toLowerCase();
    
    // Detectar confirmao para adicionar despesa
    const confirmationPatterns = [
      'sim', 'sim claro', 'sim, mas', 'sim mas', 'sim mas no',
      'sim adicionar', 'sim adiciona', 'sim adicione',
      'sim ok', 'sim tudo bem', 'sim pode', 'sim pode ser',
      'claro', 'claro sim', 'ok sim', 'ok claro',
      'adiciona', 'adicionar', 'adicione', 'pode adicionar',
      'confirma', 'confirmo', 'pode ser', 'tudo bem'
    ];
    
    const isConfirmation = confirmationPatterns.some(pattern => 
      lowerInput.includes(pattern) || lowerInput === pattern
    );
    
    if (isConfirmation) {
      console.log('Confirmao detectada:', input);
      return await handleAddExpenseFromChat(input);
    }
    
    // Detectar se  uma despesa para categorizar
    if (isExpenseInput(input)) {
      return await processExpenseCategorization(input);
    }
    
        // Detect questions about who Xuzinha is
        if (lowerInput.includes('quem ') || lowerInput.includes('quem voce') || lowerInput.includes('quem vc') || lowerInput.includes('who are you') || lowerInput.includes('what are you')) {
          return "I'm Xuzinha! \n\nYour personal financial assistant with advanced AI! I can:\n Categorize expenses automatically\n Scan receipts with OCR\n Analyze your spending\n Give personalized tips\n Answer any financial questions\n\nI'm here to help you organize your financial life! ";
        }
    
    // Detectar comandos de categorizao
    if (lowerInput.includes('categorizar') || lowerInput.includes('categoria')) {
      return xuzinhaResponses.categorize[Math.floor(Math.random() * xuzinhaResponses.categorize.length)];
    }
    
    // Detectar comandos de escaneamento
    if (lowerInput.includes('escanear') || lowerInput.includes('recibo') || lowerInput.includes('foto')) {
      return xuzinhaResponses.scan[Math.floor(Math.random() * xuzinhaResponses.scan.length)];
    }
    
    // Detectar comandos de resumo
    if (lowerInput.includes('resumo') || lowerInput.includes('grfico')) {
      return xuzinhaResponses.summary[Math.floor(Math.random() * xuzinhaResponses.summary.length)];
    }
    
    // Detectar comandos de dicas
    if (lowerInput.includes('dica') || lowerInput.includes('economia') || lowerInput.includes('poupana')) {
      return xuzinhaResponses.tips[Math.floor(Math.random() * xuzinhaResponses.tips.length)];
    }
    
        // Detect greetings
        if (lowerInput.includes('ol') || lowerInput.includes('oi') || lowerInput.includes('hello') || lowerInput.includes('hi')) {
          return "Hello!  How can I help you with your finances today?";
        }
        
        // Detect thanks
        if (lowerInput.includes('obrigado') || lowerInput.includes('valeu') || lowerInput.includes('thanks') || lowerInput.includes('thank you')) {
          return "You're welcome!  I'm here whenever you need me!";
        }
    
    // Default intelligent response
    const responses = [
      "Interesting!  Tell me more details so I can help you better!",
      "I understand!  I can help with categorization, summaries or savings tips!",
      "Great question!  I'll give you the best answer based on your data!",
      "Perfect!  How can I help you with this financial question?",
      "Excellent!  I'll analyze this for you!"
    ];
    
    return responses[Math.floor(Math.random() * responses.length)];
  };

  // Detectar se o input  uma despesa
  const isExpenseInput = (input: string): boolean => {
    const expenseKeywords = [
      'gastei', 'gasto', 'comprei', 'paguei', 'reais', 'dlar', 'euro', 
      'almoo', 'jantar', 'gasolina', 'farmcia', 'supermercado', 'cinema',
      'teatro', 'shopping', 'restaurante', 'bar', 'caf', 'lanche',
      'uber', 'taxi', 'nibus', 'metro', 'combustvel', 'posto',
      'mdico', 'dentista', 'hospital', 'clnica', 'medicamento',
      'aluguel', 'condomnio', 'luz', 'gua', 'internet', 'telefone',
      'curso', 'livro', 'escola', 'universidade', 'aula',
      'jogo', 'filme', 'srie', 'netflix', 'spotify', 'streaming',
      'roupa', 'sapato', 'acessrio', 'presente', 'loja'
    ];
    const lowerInput = input.toLowerCase();
    return expenseKeywords.some(keyword => lowerInput.includes(keyword)) || /\d+/.test(input);
  };

  // Processar categorizao de despesa
  const processExpenseCategorization = async (input: string): Promise<string> => {
    try {
      const response = await expenseService.categorizeExpense(input);
      const categoryLabels: { [key: string]: string } = {
        'food': 'Alimentao',
        'transport': 'Transporte',
        'health': 'Sade',
        'housing': 'Moradia',
        'leisure': 'Lazer',
        'education': 'Educao',
        'utilities': 'Utilidades',
        'shopping': 'Compras',
        'entertainment': 'Entretenimento',
        'other': 'Outros'
      };
      
      const categoryLabel = categoryLabels[response.category] || response.category;
      const confidence = Math.round(response.confidence * 100);
      
      // Armazenar dados da despesa para possvel adio
      const expenseData = {
        description: input,
        amount: extractAmountFromText(input),
        category: response.category,
        date: new Date().toISOString().split('T')[0]
      };
      
      // Armazenar no estado para uso posterior
      setMessages(prev => [...prev, {
        id: `expense-${Date.now()}`,
        text: `Dados da despesa extrados:\n **Valor:** R$ ${expenseData.amount}\n **Data:** ${expenseData.date}`,
        isUser: false,
        timestamp: new Date(),
        type: 'action',
        data: expenseData
      }]);
      
          return `Perfect! I categorized your expense! \n\n **Description:** ${input}\n **Category:** ${categoryLabel}\n **Confidence:** ${confidence}%\n **Amount:** $${expenseData.amount}\n\nWant me to add this expense to your record?`;
    } catch (error) {
      console.error('Error categorizing expense:', error);
      return "Oops! I couldn't categorize your expense. Try describing it better, for example: 'Lunch at restaurant 45 reais' ";
    }
  };

  // Extrair valor do texto
  const extractAmountFromText = (text: string): number => {
    // Padres mais especficos para valores brasileiros
    const patterns = [
      /(\d+(?:\.\d{3})*,\d{2})/,  // 1.234,56
      /(\d+,\d{2})/,              // 123,45
      /(\d+(?:\.\d{2})?)/,        // 123.45 ou 123
      /r\$\s*(\d+(?:\.\d{2})?)/i, // R$ 123.45
      /reais?\s*(\d+(?:\.\d{2})?)/i, // 123 reais
    ];
    
    for (const pattern of patterns) {
      const match = text.match(pattern);
      if (match) {
        let value = match[1];
        // Converter formato brasileiro para decimal
        if (value.includes(',')) {
          value = value.replace('.', '').replace(',', '.');
        }
        const amount = parseFloat(value);
        if (amount > 0) {
          return amount;
        }
      }
    }
    
    return 0;
  };

  // Adicionar despesa a partir do chat
  const handleAddExpenseFromChat = async (input: string): Promise<string> => {
    try {
      console.log('Tentando adicionar despesa...');
      console.log('Mensagens disponveis:', messages.filter(msg => msg.type === 'action'));
      
      // Buscar a ltima despesa categorizada
      const lastExpenseMessage = messages
        .filter(msg => msg.type === 'action' && msg.data)
        .pop();
      
      console.log('ltima mensagem de despesa:', lastExpenseMessage);
      
          if (!lastExpenseMessage || !lastExpenseMessage.data) {
            return "Oops! I couldn't find expense data to add. Try describing your expense again! ";
          }
      
      const expenseData = lastExpenseMessage.data;
      console.log('Dados da despesa:', expenseData);
      
      // Adicionar despesa via API
      const response = await expenseService.addExpense({
        description: expenseData.description,
        amount: expenseData.amount,
        category: expenseData.category,
        date: expenseData.date
      });
      
      console.log('Resposta da API:', response);
      
          addToast({
            type: 'success',
            title: 'Expense added!',
            message: `$${expenseData.amount} added to ${expenseData.category}`
          });
      
      // Notificar o Dashboard para atualizar os dados
      onExpenseAdded?.();
      
      return `Excellent!  Expense added successfully!\n\n **Description:** ${expenseData.description}\n **Amount:** $${expenseData.amount}\n **Category:** ${expenseData.category}\n **Date:** ${expenseData.date}\n\nYour expense has been recorded and is already updating your budget! `;
      
    } catch (error) {
      console.error('Error adding expense:', error);
      addToast({
        type: 'error',
        title: 'Error adding expense',
        message: 'Please try again in a few moments'
      });
      return "Oops! I couldn't add your expense. Please try again! ";
    }
  };

  // Funo para escanear recibo
  const handleReceiptScan = async (file: File) => {
    setIsScanning(true);
    
    try {
      // Simular OCR (em produo, voc usaria uma API real)
      const mockReceiptData = {
        description: "Receipt from " + file.name,
        amount: Math.random() * 100 + 10,
        category: "food",
        date: new Date().toISOString().split('T')[0],
        merchant: "Estabelecimento"
      };
      
      const message: Message = {
        id: Date.now().toString(),
        text: `Recibo escaneado com sucesso! \n\n **Descrio:** ${mockReceiptData.description}\n **Valor:** R$ ${mockReceiptData.amount.toFixed(2)}\n **Categoria:** ${mockReceiptData.category}\n **Data:** ${mockReceiptData.date}\n **Estabelecimento:** ${mockReceiptData.merchant}\n\nQuer que eu adicione esta despesa?`,
        isUser: false,
        timestamp: new Date(),
        type: 'action',
        data: mockReceiptData
      };
      
      setMessages(prev => [...prev, message]);
      addToast({
        type: 'success',
        title: 'Receipt scanned successfully!',
        message: 'Data extracted successfully'
      });
    } catch (error) {
      console.error('Error scanning receipt:', error);
      addToast({
        type: 'error',
        title: 'Error scanning receipt',
        message: 'Please try again with a clearer image'
      });
    } finally {
      setIsScanning(false);
    }
  };


  const handleQuickAction = (action: string) => {
    if (action === 'scan') {
      fileInputRef.current?.click();
      return;
    }
    
    const response = xuzinhaResponses[action as keyof typeof xuzinhaResponses][0];
    const message: Message = {
      id: Date.now().toString(),
      text: response,
      isUser: false,
      timestamp: new Date(),
      type: 'suggestion'
    };
    
    setMessages(prev => [...prev, message]);
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      handleReceiptScan(file);
    }
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString('pt-BR', { 
      hour: '2-digit', 
      minute: '2-digit' 
    });
  };

  return (
    <>
      {/* Boto flutuante da Xuzinha */}
      {!isOpen && (
        <div className="fixed bottom-6 right-6 z-50">
          <Button
            onClick={() => setIsOpen(true)}
            className="rounded-full w-16 h-16 bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700 shadow-lg hover:shadow-xl transition-all duration-300 animate-bounce-soft"
          >
            <Bot className="h-8 w-8 text-white" />
          </Button>
          <div className="absolute -top-2 -right-2 w-6 h-6 bg-red-500 rounded-full flex items-center justify-center">
            <span className="text-xs text-white font-bold">1</span>
          </div>
        </div>
      )}

      {/* Chat da Xuzinha */}
      {isOpen && (
        <div className="fixed bottom-6 right-6 z-50 w-96 h-[500px] bg-white rounded-xl shadow-2xl border border-gray-200 flex flex-col">
          {/* Header */}
          <div className="bg-gradient-to-r from-purple-600 to-pink-600 text-white p-4 rounded-t-xl flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-white bg-opacity-20 rounded-full flex items-center justify-center">
                <Bot className="h-6 w-6" />
              </div>
              <div>
                <h3 className="font-semibold">Xuzinha</h3>
                <p className="text-xs opacity-90">Assistente Financeira</p>
              </div>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={() => setIsOpen(false)}
              className="text-white border-white hover:bg-white hover:text-purple-600"
            >
              <X className="h-4 w-4" />
            </Button>
          </div>

          {/* Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
              >
                <div
                  className={`max-w-xs p-3 rounded-lg ${
                    message.isUser
                      ? 'bg-primary-600 text-white'
                      : 'bg-gray-100 text-gray-900'
                  }`}
                >
                  <p className="text-sm whitespace-pre-line">{message.text}</p>
                  <p className="text-xs opacity-70 mt-1">
                    {formatTime(message.timestamp)}
                  </p>
                </div>
              </div>
            ))}
            
            {isTyping && (
              <div className="flex justify-start">
                <div className="bg-gray-100 text-gray-900 p-3 rounded-lg">
                  <div className="flex space-x-1">
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>

          {/* Quick Actions */}
          <div className="p-4 border-t border-gray-200">
            <div className="grid grid-cols-2 gap-2 mb-3">
              {quickActions.map((action, index) => (
                <Button
                  key={index}
                  variant="outline"
                  size="sm"
                  onClick={() => handleQuickAction(action.action)}
                  className="text-xs"
                >
                  <action.icon className="h-3 w-3 mr-1" />
                  {action.text}
                </Button>
              ))}
            </div>

            {/* Input */}
            <div className="flex space-x-2">
              <input
                type="text"
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                placeholder="Type your message..."
                className="flex-1 px-3 py-2 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-purple-500"
                disabled={isTyping || isScanning}
              />
              <Button
                onClick={() => fileInputRef.current?.click()}
                disabled={isTyping || isScanning}
                size="sm"
                variant="outline"
                className="text-purple-600 border-purple-600 hover:bg-purple-50"
                title="Escanear recibo"
              >
                {isScanning ? (
                  <div className="w-4 h-4 border-2 border-purple-600 border-t-transparent rounded-full animate-spin" />
                ) : (
                  <Camera className="h-4 w-4" />
                )}
              </Button>
              <Button
                onClick={handleSendMessage}
                disabled={!inputText.trim() || isTyping || isScanning}
                size="sm"
                className="bg-purple-600 hover:bg-purple-700"
              >
                <Send className="h-4 w-4" />
              </Button>
            </div>
            
            {/* Hidden file input */}
            <input
              ref={fileInputRef}
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              className="hidden"
            />
          </div>
        </div>
      )}
    </>
  );
};

export default XuzinhaChat;
