export interface Expense {
  id: number;
  description: string;
  amount: number;
  category: string;
  date: string;
  created_at: string;
}

export interface ExpenseInput {
  description: string;
  amount: number;
  category: string;
  date?: string;
}

export interface Income {
  id: number;
  description: string;
  amount: number;
  source: string;
  date: string;
  created_at: string;
}

export interface IncomeInput {
  description: string;
  amount: number;
  source: string;
  date?: string;
}

export interface Budget {
  id: number;
  category: string;
  budget_amount: number;
  spent_amount: number;
  remaining_amount: number;
  month: string; // YYYY-MM
  created_at: string;
}

export interface BudgetInput {
  category: string;
  budget_amount: number;
  month: string;
}

export interface CategorySummary {
  category: string;
  count: number;
  total: number;
  average: number;
  budget_amount: number;
  remaining_amount: number;
  percentage_used: number;
}

export interface MonthlySummary {
  month: string;
  total_income: number;
  total_expenses: number;
  total_savings: number;
  savings_percentage: number;
  remaining_balance: number;
  budget_summary: CategorySummary[];
}

export interface Summary {
  categories: CategorySummary[];
  total_amount: number;
  total_income: number;
  total_savings: number;
  total_count: number;
  monthly_summary: MonthlySummary;
  recent_expense: {
    description: string;
    amount: number;
    date: string;
  } | null;
}

export interface CategorizeResponse {
  category: string;
  confidence: number;
}

export type CategoryType = 'food' | 'transport' | 'health' | 'housing' | 'leisure' | 'education' | 'utilities' | 'shopping' | 'entertainment' | 'other';

