import axios from 'axios';
import type { Expense, ExpenseInput, Summary, CategorizeResponse } from '../types';

import { API_CONFIG } from '../config/api';

const API_BASE_URL = API_CONFIG.BASE_URL;

const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000,
});

// Request interceptor for logging
api.interceptors.request.use(
  (config) => {
    console.log(`[API] ${config.method?.toUpperCase()} ${config.url}`);
    return config;
  },
  (error) => {
    console.error('[API] Request error:', error);
    return Promise.reject(error);
  }
);

// Response interceptor for error handling
api.interceptors.response.use(
  (response) => {
    console.log(`[API] Response:`, response.data);
    return response;
  },
  (error) => {
    console.error('[API] Response error:', error.response?.data || error.message);
    return Promise.reject(error);
  }
);

export const expenseService = {
  // Get all expenses
  getExpenses: async (days?: number, limit?: number): Promise<Expense[]> => {
    // Always use offline mode for mobile compatibility
    const expenses = JSON.parse(localStorage.getItem('xubudget_expenses') || '[]');
    return expenses;
  },

  // Add new expense
  addExpense: async (expense: ExpenseInput): Promise<Expense> => {
    // Always use offline mode for mobile compatibility
    const newExpense: Expense = {
      id: Date.now(),
      description: expense.description,
      amount: expense.amount,
      category: expense.category,
      date: expense.date || new Date().toISOString().split('T')[0],
      created_at: new Date().toISOString()
    };
    
    // Store in localStorage for offline use
    const expenses = JSON.parse(localStorage.getItem('xubudget_expenses') || '[]');
    expenses.unshift(newExpense);
    localStorage.setItem('xubudget_expenses', JSON.stringify(expenses));
    
    return newExpense;
  },

  // Delete expense
  deleteExpense: async (id: number): Promise<void> => {
    // Always use offline mode for mobile compatibility
    const expenses = JSON.parse(localStorage.getItem('xubudget_expenses') || '[]');
    const filteredExpenses = expenses.filter((exp: Expense) => exp.id !== id);
    localStorage.setItem('xubudget_expenses', JSON.stringify(filteredExpenses));
  },

  // Get expenses by category
  getByCategory: async (category?: string): Promise<Expense[]> => {
    // Always use offline mode for mobile compatibility
    const expenses = JSON.parse(localStorage.getItem('xubudget_expenses') || '[]');
    if (category) {
      return expenses.filter((exp: Expense) => exp.category === category);
    }
    return expenses;
  },

  // Get summary
  getSummary: async (): Promise<Summary> => {
    // Always use offline mode for mobile compatibility
    const expenses = JSON.parse(localStorage.getItem('xubudget_expenses') || '[]');
    const total_amount = expenses.reduce((sum: number, exp: Expense) => sum + exp.amount, 0);
    const total_income = 5000;
    const total_savings = total_income - total_amount;
    
    // Group by category
    const categoryMap = expenses.reduce((acc: any, exp: Expense) => {
      if (!acc[exp.category]) {
        acc[exp.category] = { count: 0, total: 0 };
      }
      acc[exp.category].count++;
      acc[exp.category].total += exp.amount;
      return acc;
    }, {});
    
    const categories = Object.entries(categoryMap).map(([category, data]: [string, any]) => ({
      category,
      count: data.count,
      total: data.total,
      average: data.total / data.count,
      budget_amount: 1000, // Default budget
      remaining_amount: 1000 - data.total,
      percentage_used: (data.total / 1000) * 100
    }));
    
    return {
      categories,
      total_amount,
      total_income,
      total_savings,
      total_count: expenses.length,
      monthly_summary: {
        month: new Date().toISOString().slice(0, 7),
        total_income,
        total_expenses: total_amount,
        total_savings,
        savings_percentage: (total_savings / total_income) * 100,
        remaining_balance: total_savings,
        budget_summary: categories
      },
      recent_expense: expenses.length > 0 ? {
        description: expenses[0].description,
        amount: expenses[0].amount,
        date: expenses[0].date
      } : null
    };
  },

  // Categorize expense text
  categorize: async (text: string): Promise<CategorizeResponse> => {
    try {
      const response = await api.post<CategorizeResponse>('/categorize', { text });
      return response.data;
    } catch (error) {
      // Offline categorization
      const lowerText = text.toLowerCase();
      let category = 'other';
      let confidence = 0.5;
      
      if (lowerText.includes('almoo') || lowerText.includes('restaurante') || lowerText.includes('comida') || lowerText.includes('lanche')) {
        category = 'food';
        confidence = 0.8;
      } else if (lowerText.includes('gasolina') || lowerText.includes('uber') || lowerText.includes('transporte') || lowerText.includes('nibus')) {
        category = 'transport';
        confidence = 0.8;
      } else if (lowerText.includes('farmcia') || lowerText.includes('mdico') || lowerText.includes('sade') || lowerText.includes('medicamento')) {
        category = 'health';
        confidence = 0.8;
      } else if (lowerText.includes('cinema') || lowerText.includes('lazer') || lowerText.includes('jogo') || lowerText.includes('entretenimento')) {
        category = 'leisure';
        confidence = 0.8;
      } else if (lowerText.includes('supermercado') || lowerText.includes('compras') || lowerText.includes('loja')) {
        category = 'shopping';
        confidence = 0.8;
      }
      
      return { category, confidence };
    }
  },

  // Categorize expense text (alias)
  categorizeExpense: async (text: string): Promise<CategorizeResponse> => {
    return expenseService.categorize(text);
  },

  // Scan receipt
  scanReceipt: async (text: string): Promise<any> => {
    const response = await api.post('/scan-receipt', { text });
    return response.data;
  },

  // Health check
  health: async (): Promise<{ status: string; service: string; version: string }> => {
    const response = await api.get('/health');
    return response.data;
  },
};

export default api;
