import React, { useState } from 'react';
import { Plus } from 'lucide-react';
import { Card, CardHeader, CardBody } from './ui/Card';
import { Button } from './ui/Button';
import { Input } from './ui/Input';
import { useToast } from './ui/Toast';
import { INCOME_SOURCES, type IncomeInput } from '../types';

interface AddIncomeProps {
  onIncomeAdded?: (income: IncomeInput) => void;
  onClose?: () => void;
}

const AddIncome: React.FC<AddIncomeProps> = ({ onIncomeAdded, onClose }) => {
  const { addToast } = useToast();
  const [description, setDescription] = useState('');
  const [amount, setAmount] = useState<number | ''>('');
  const [source, setSource] = useState('Salary');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!description || amount === '' || !source || !date) {
      addToast({
        type: 'warning',
        title: 'Required fields',
        message: 'Please fill in all fields.'
      });
      return;
    }

    if (Number(amount) <= 0) {
      addToast({
        type: 'warning',
        title: 'Invalid amount',
        message: 'Amount must be greater than zero.'
      });
      return;
    }

    setIsLoading(true);
    
    try {
      const incomeData: IncomeInput = {
        description: description.trim(),
        amount: Number(amount),
        source,
        date
      };

      // Simular salvamento (em produo, chamaria a API)
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      addToast({
        type: 'success',
        title: 'Income added!',
        message: `${source} of $${Number(amount).toFixed(2)} added successfully.`
      });

      // Reset form
      setDescription('');
      setAmount('');
      setSource('Salary');
      setDate(new Date().toISOString().split('T')[0]);

      onIncomeAdded?.(incomeData);
      onClose?.();
      
    } catch (error) {
      addToast({
        type: 'error',
        title: 'Error adding income',
        message: 'Please try again in a few moments.'
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center space-x-2">
          <Plus className="h-5 w-5 text-green-600" />
          <h2 className="text-xl font-semibold text-gray-900">Add Income</h2>
        </div>
      </CardHeader>
      <CardBody>
        <form onSubmit={handleSubmit} className="space-y-4">
          <Input
            label="Description"
            placeholder="Ex: Monthly salary, Freelance, Bonus..."
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            required
          />

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Input
              label="Amount"
              type="number"
              placeholder="Ex: 5000.00"
              value={amount}
              onChange={(e) => setAmount(parseFloat(e.target.value))}
              step="0.01"
              min="0"
              required
            />

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Source
              </label>
              <select
                value={source}
                onChange={(e) => setSource(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
                required
              >
                {INCOME_SOURCES.map((incomeSource) => (
                  <option key={incomeSource} value={incomeSource}>
                    {incomeSource}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <Input
            label="Date"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />

          <div className="flex justify-end space-x-3 pt-4">
            {onClose && (
              <Button type="button" onClick={onClose} variant="outline">
                Cancel
              </Button>
            )}
            <Button 
              type="submit" 
              disabled={isLoading}
              className="bg-green-600 hover:bg-green-700"
            >
              {isLoading ? (
                <div className="flex items-center">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2" />
                  Adding...
                </div>
              ) : (
                <>
                  <Plus className="h-4 w-4 mr-2" />
                  Add Income
                </>
              )}
            </Button>
          </div>
        </form>
      </CardBody>
    </Card>
  );
};

export default AddIncome;
