import React, { useState } from 'react';
import { Plus, Wand2 } from 'lucide-react';
import { useExpenses } from '../hooks/useExpenses';
import { expenseService } from '../services/api';
import { Card, CardHeader, CardBody } from './ui/Card';
import Button from './ui/Button';
import Input from './ui/Input';
import { useToast } from './ui/Toast';
import { CATEGORY_LABELS } from '../types';

interface AddExpenseProps {
  onExpenseAdded?: () => void;
}

const AddExpense: React.FC<AddExpenseProps> = ({ onExpenseAdded }) => {
  const { addExpense } = useExpenses();
  const { addToast } = useToast();
  
  const [formData, setFormData] = useState({
    description: '',
    amount: '',
    category: '',
    date: new Date().toISOString().split('T')[0],
  });
  
  const [loading, setLoading] = useState(false);
  const [categorizing, setCategorizing] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.description.trim()) {
      newErrors.description = 'Description is required';
    }
    
    if (!formData.amount || parseFloat(formData.amount) <= 0) {
      newErrors.amount = 'Amount must be greater than 0';
    }
    
    if (!formData.category) {
      newErrors.category = 'Category is required';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleCategorize = async () => {
    if (!formData.description.trim()) {
      addToast({
        type: 'warning',
        title: 'Description required',
        message: 'Please enter a description before categorizing.',
      });
      return;
    }

    try {
      setCategorizing(true);
      const result = await expenseService.categorize(formData.description);
      
      setFormData(prev => ({
        ...prev,
        category: result.category,
      }));
      
      addToast({
        type: 'success',
        title: 'Category detected!',
        message: `Suggested category: ${CATEGORY_LABELS[result.category] || result.category}`,
      });
    } catch (error) {
      addToast({
        type: 'error',
        title: 'Categorization failed',
        message: 'Could not auto-categorize. Please select manually.',
      });
    } finally {
      setCategorizing(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    try {
      setLoading(true);
      
      const expenseData = {
        description: formData.description.trim(),
        amount: parseFloat(formData.amount),
        category: formData.category,
        date: formData.date,
      };

      await addExpense(expenseData);
      
      // Reset form
      setFormData({
        description: '',
        amount: '',
        category: '',
        date: new Date().toISOString().split('T')[0],
      });
      
      addToast({
        type: 'success',
        title: 'Expense added!',
        message: `${expenseData.description} - ${expenseData.amount.toLocaleString('en-US', { style: 'currency', currency: 'USD' })}`,
      });
      
      onExpenseAdded?.();
    } catch (error) {
      addToast({
        type: 'error',
        title: 'Failed to add expense',
        message: 'Please try again.',
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center space-x-2">
          <Plus className="h-5 w-5 text-primary-600" />
          <h2 className="text-lg font-semibold text-gray-900">Add New Expense</h2>
        </div>
      </CardHeader>
      <CardBody>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="md:col-span-2">
              <Input
                label="Description"
                placeholder="e.g., Lunch at restaurant"
                value={formData.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                error={errors.description}
                required
              />
            </div>
            
            <div>
              <Input
                label="Amount"
                type="number"
                step="0.01"
                min="0"
                placeholder="0.00"
                value={formData.amount}
                onChange={(e) => handleInputChange('amount', e.target.value)}
                error={errors.amount}
                required
              />
            </div>
            
            <div>
              <Input
                label="Date"
                type="date"
                value={formData.date}
                onChange={(e) => handleInputChange('date', e.target.value)}
                required
              />
            </div>
            
            <div className="md:col-span-2">
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">
                  Category
                </label>
                <div className="flex space-x-2">
                  <select
                    className="flex-1 rounded-lg border border-gray-300 px-3 py-2 text-sm focus:border-primary-500 focus:outline-none focus:ring-1 focus:ring-primary-500"
                    value={formData.category}
                    onChange={(e) => handleInputChange('category', e.target.value)}
                  >
                    <option value="">Select a category</option>
                    {Object.entries(CATEGORY_LABELS).map(([key, label]) => (
                      <option key={key} value={key}>
                        {label}
                      </option>
                    ))}
                  </select>
                  <Button
                    type="button"
                    variant="outline"
                    onClick={handleCategorize}
                    loading={categorizing}
                    disabled={!formData.description.trim()}
                  >
                    <Wand2 className="h-4 w-4 mr-1" />
                    Auto
                  </Button>
                </div>
                {errors.category && (
                  <p className="text-sm text-danger-600">{errors.category}</p>
                )}
              </div>
            </div>
          </div>
          
          <div className="flex justify-end space-x-3 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => {
                setFormData({
                  description: '',
                  amount: '',
                  category: '',
                  date: new Date().toISOString().split('T')[0],
                });
                setErrors({});
              }}
            >
              Clear
            </Button>
            <Button
              type="submit"
              loading={loading}
              disabled={loading || categorizing}
            >
              <Plus className="h-4 w-4 mr-1" />
              Add Expense
            </Button>
          </div>
        </form>
      </CardBody>
    </Card>
  );
};

export default AddExpense;
