import React from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';
import { useSummary } from '../hooks/useExpenses';
import { formatCurrency } from '../utils/format';
import { Card, CardHeader, CardBody } from './ui/Card';
import Loading from './ui/Loading';
import { DollarSign, TrendingUp, Calendar, Tag } from 'lucide-react';
import { CATEGORY_COLORS, CATEGORY_LABELS } from '../types';

const Summary: React.FC = () => {
  const { summary, loading, error } = useSummary();

  if (loading) {
    return (
      <Card>
        <CardBody>
          <Loading size="lg" text="Loading summary..." />
        </CardBody>
      </Card>
    );
  }

  if (error) {
    return (
      <Card>
        <CardBody>
          <div className="text-center py-8">
            <div className="text-danger-600 mb-2">Error loading summary</div>
            <p className="text-gray-600">{error}</p>
          </div>
        </CardBody>
      </Card>
    );
  }

  if (!summary) {
    return null;
  }

  // Prepare data for charts
  const pieData = summary.categories.map(cat => ({
    name: CATEGORY_LABELS[cat.category] || cat.category,
    value: cat.total,
    count: cat.count,
    color: CATEGORY_COLORS[cat.category] || CATEGORY_COLORS.other,
  }));

  const barData = summary.categories.map(cat => ({
    name: CATEGORY_LABELS[cat.category] || cat.category,
    amount: cat.total,
    count: cat.count,
    average: cat.average,
  }));

  const COLORS = pieData.map(item => item.color);

  return (
    <div className="space-y-6">
      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card>
          <CardBody>
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <DollarSign className="h-8 w-8 text-primary-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Total Amount</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {formatCurrency(summary.total_amount)}
                </p>
              </div>
            </div>
          </CardBody>
        </Card>

        <Card>
          <CardBody>
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <TrendingUp className="h-8 w-8 text-success-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Total Expenses</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {summary.total_count}
                </p>
              </div>
            </div>
          </CardBody>
        </Card>

        <Card>
          <CardBody>
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <Calendar className="h-8 w-8 text-warning-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Categories</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {summary.categories.length}
                </p>
              </div>
            </div>
          </CardBody>
        </Card>

        <Card>
          <CardBody>
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <Tag className="h-8 w-8 text-danger-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-500">Average</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {formatCurrency(summary.total_amount / summary.total_count)}
                </p>
              </div>
            </div>
          </CardBody>
        </Card>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Pie Chart */}
        <Card>
          <CardHeader>
            <h3 className="text-lg font-semibold text-gray-900">Expenses by Category</h3>
          </CardHeader>
          <CardBody>
            <div className="h-80">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={pieData}
                    cx="50%"
                    cy="50%"
                    labelLine={false}
                    label={({ name, percent }: any) => `${name} ${(percent * 100).toFixed(0)}%`}
                    outerRadius={80}
                    fill="#8884d8"
                    dataKey="value"
                  >
                    {pieData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip formatter={(value) => formatCurrency(Number(value))} />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </CardBody>
        </Card>

        {/* Bar Chart */}
        <Card>
          <CardHeader>
            <h3 className="text-lg font-semibold text-gray-900">Amount by Category</h3>
          </CardHeader>
          <CardBody>
            <div className="h-80">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={barData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="name" />
                  <YAxis />
                  <Tooltip formatter={(value) => formatCurrency(Number(value))} />
                  <Bar dataKey="amount" fill="#3b82f6" />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardBody>
        </Card>
      </div>

      {/* Category Breakdown */}
      <Card>
        <CardHeader>
          <h3 className="text-lg font-semibold text-gray-900">Category Breakdown</h3>
        </CardHeader>
        <CardBody>
          <div className="space-y-4">
            {summary.categories.map((category) => (
              <div key={category.category} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-3">
                  <div
                    className="w-4 h-4 rounded-full"
                    style={{ backgroundColor: CATEGORY_COLORS[category.category] || CATEGORY_COLORS.other }}
                  />
                  <div>
                    <p className="font-medium text-gray-900">
                      {CATEGORY_LABELS[category.category] || category.category}
                    </p>
                    <p className="text-sm text-gray-500">
                      {category.count} expense{category.count !== 1 ? 's' : ''}
                    </p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="font-semibold text-gray-900">
                    {formatCurrency(category.total)}
                  </p>
                  <p className="text-sm text-gray-500">
                    Avg: {formatCurrency(category.average)}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </CardBody>
      </Card>

      {/* Recent Expense */}
      {summary.recent_expense && (
        <Card>
          <CardHeader>
            <h3 className="text-lg font-semibold text-gray-900">Most Recent Expense</h3>
          </CardHeader>
          <CardBody>
            <div className="flex items-center justify-between p-4 bg-primary-50 rounded-lg">
              <div>
                <p className="font-medium text-gray-900">{summary.recent_expense.description}</p>
                <p className="text-sm text-gray-500">{summary.recent_expense.date}</p>
              </div>
              <div className="text-right">
                <p className="font-semibold text-primary-600">
                  {formatCurrency(summary.recent_expense.amount)}
                </p>
              </div>
            </div>
          </CardBody>
        </Card>
      )}
    </div>
  );
};

export default Summary;
