import { useState, useEffect, useCallback } from 'react';
import { expenseService } from '../services/api';
import type { Expense, ExpenseInput, Summary } from '../types';

export const useExpenses = () => {
  const [expenses, setExpenses] = useState<Expense[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchExpenses = useCallback(async (days?: number, limit?: number) => {
    try {
      setLoading(true);
      setError(null);
      const data = await expenseService.getExpenses(days, limit);
      setExpenses(data);
    } catch (err: any) {
      setError(err.message || 'Failed to fetch expenses');
      console.error('Error fetching expenses:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  const addExpense = useCallback(async (expense: ExpenseInput) => {
    try {
      const newExpense = await expenseService.addExpense(expense);
      setExpenses(prev => [newExpense, ...prev]);
      return newExpense;
    } catch (err: any) {
      setError(err.message || 'Failed to add expense');
      throw err;
    }
  }, []);

  const deleteExpense = useCallback(async (id: number) => {
    try {
      await expenseService.deleteExpense(id);
      setExpenses(prev => prev.filter(exp => exp.id !== id));
    } catch (err: any) {
      setError(err.message || 'Failed to delete expense');
      throw err;
    }
  }, []);

  useEffect(() => {
    fetchExpenses();
  }, [fetchExpenses]);

  return {
    expenses,
    loading,
    error,
    fetchExpenses,
    addExpense,
    deleteExpense,
  };
};

export const useSummary = () => {
  const [summary, setSummary] = useState<Summary | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchSummary = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const data = await expenseService.getSummary();
      setSummary(data);
    } catch (err: any) {
      setError(err.message || 'Failed to fetch summary');
      console.error('Error fetching summary:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchSummary();
  }, [fetchSummary]);

  return {
    summary,
    loading,
    error,
    fetchSummary,
  };
};
