import React, { useState, useEffect } from 'react';
import { X, Target, Save, AlertCircle } from 'lucide-react';
import { Card, CardHeader, CardBody } from './ui/Card';
import { Button } from './ui/Button';
import { Input } from './ui/Input';
import { Loading } from './ui/Loading';
import { 
  CATEGORY_COLORS, 
  CATEGORY_ICONS, 
  CATEGORY_LABELS, 
  type BudgetInput 
} from '../types';
import { formatCurrency } from '../utils/format';

interface BudgetManagerProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (budgets: BudgetInput[]) => void;
  currentBudgets: { [category: string]: number };
  loading?: boolean;
}

const BudgetManager: React.FC<BudgetManagerProps> = ({
  isOpen,
  onClose,
  onSave,
  currentBudgets,
  loading = false
}) => {
  const [budgets, setBudgets] = useState<{ [category: string]: number }>({});
  const [selectedMonth, setSelectedMonth] = useState(new Date().toISOString().slice(0, 7));

  useEffect(() => {
    if (isOpen) {
      setBudgets(currentBudgets);
    }
  }, [isOpen, currentBudgets]);

  const categories = Object.keys(CATEGORY_LABELS);

  const handleBudgetChange = (category: string, value: string) => {
    const amount = parseFloat(value) || 0;
    setBudgets(prev => ({
      ...prev,
      [category]: amount
    }));
  };

  const handleSave = () => {
    const budgetInputs: BudgetInput[] = categories
      .filter(category => budgets[category] > 0)
      .map(category => ({
        category,
        budget_amount: budgets[category],
        month: selectedMonth
      }));
    
    onSave(budgetInputs);
  };

  const totalBudget = Object.values(budgets).reduce((sum, amount) => sum + amount, 0);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <Card className="w-full max-w-2xl max-h-[90vh] overflow-y-auto">
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <Target className="h-5 w-5 text-primary-600" />
              <h2 className="text-xl font-semibold text-gray-900">Manage Budget</h2>
            </div>
            <Button onClick={onClose} variant="outline" size="sm">
              <X className="h-4 w-4" />
            </Button>
          </div>
        </CardHeader>
        
        <CardBody className="space-y-6">
          {/* Month Selection */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Budget Month
            </label>
            <input
              type="month"
              value={selectedMonth}
              onChange={(e) => setSelectedMonth(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500"
            />
          </div>

          {/* Budget by Category */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium text-gray-900">Budget by Category</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {categories.map((category) => (
                <div key={category} className="border border-gray-200 rounded-lg p-4">
                  <div className="flex items-center space-x-3 mb-3">
                    <div 
                      className="w-8 h-8 rounded-full flex items-center justify-center text-white text-sm"
                      style={{ backgroundColor: CATEGORY_COLORS[category] }}
                    >
                      {CATEGORY_ICONS[category]}
                    </div>
                    <div>
                      <h4 className="font-medium text-gray-900">{CATEGORY_LABELS[category]}</h4>
                    </div>
                  </div>
                  <Input
                    type="number"
                    placeholder="0.00"
                    value={budgets[category] || ''}
                    onChange={(e) => handleBudgetChange(category, e.target.value)}
                    step="0.01"
                    min="0"
                  />
                </div>
              ))}
            </div>
          </div>

          {/* Budget Summary */}
          <div className="bg-gray-50 rounded-lg p-4">
            <div className="flex items-center justify-between mb-2">
              <span className="text-sm font-medium text-gray-700">Total Budget</span>
              <span className="text-lg font-bold text-gray-900">{formatCurrency(totalBudget)}</span>
            </div>
            <div className="text-xs text-gray-500">
              Distribute your budget across categories to control your spending
            </div>
          </div>

          {/* Budget Tips */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <div className="flex items-start space-x-2">
              <AlertCircle className="h-5 w-5 text-blue-600 mt-0.5" />
              <div className="text-sm text-blue-800">
                <p className="font-medium mb-1">Tips for an effective budget:</p>
                <ul className="list-disc list-inside space-y-1 text-xs">
                  <li>Food: 15-20% of income</li>
                  <li>Housing: maximum 30% of income</li>
                  <li>Transportation: 10-15% of income</li>
                  <li>Savings: minimum 20% of income</li>
                  <li>Leisure: 5-10% of income</li>
                </ul>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex justify-end space-x-3 pt-4 border-t border-gray-200">
            <Button onClick={onClose} variant="outline">
              Cancel
            </Button>
            <Button 
              onClick={handleSave} 
              disabled={loading || totalBudget === 0}
              className="bg-primary-600 hover:bg-primary-700"
            >
              {loading ? (
                <Loading size="sm" className="!py-0 !space-y-0" />
              ) : (
                <>
                  <Save className="h-4 w-4 mr-2" />
                  Save Budget
                </>
              )}
            </Button>
          </div>
        </CardBody>
      </Card>
    </div>
  );
};

export default BudgetManager;
